@media screen and (max-width: 640px){
	.Content{display:flex;flex-direction:column;}
	.Content .ContentLeft{width:100%;}
	.Content .ContentLeft img{width:100%;}
	.Content .ContentRight{width:100%;margin-top:-5px;}
	.Content .ContentRight .CenterContent h4{color:white;}
	.Content .ContentRight .CenterContent p{width:80%;color:beige;margin-left:calc(10% - 20px);padding:20px;}
	.Content .ContentRight .CenterContent table tfoot tr td button{width:40px;height:40px;margin:10px;}
}