@media screen and (max-width: 640px){
	.Menu{float:none;position:relative;width:100%;height:175px;display:flex;text-align:center;flex-direction:column;}
	.Menu .Logo{text-align:center;width:100%;display:inline-block;}
	.Menu .Logo img{height:40px;padding:5px;}
	.Menu .Logo h1{color:white;display:flex;padding:10px;margin:0;width:100%;flex-direction:column;}
	.Menu .Items{display:flex;flex-direction:column;}
	.Menu .Items a{padding:10px;width:100%;display:flex;flex-direction:column;color:white;text-decoration:none;height:auto;}
}