.Download{height:375px;background:rgba(0,0,0,0.9);position:relative;margin-top:50px;}
.Download .ImageContent{width:117px;margin-left:calc(50% - 117px);transform:translate(50%,0);padding-top:20px;}
.Download .ImageContent img{width:113px;border:2px solid white;}
.Download .ImageContent .SpanPosition{background:white;width:100%;margin-top:-150px;z-index:5;}
.Download .ImageContent .SpanPosition p{position:absolute;padding:5px;background:white;}
/* .Download .ImageContent .SpanPosition p i{padding:5px;background:white;z-index:5;} */
.Download .PositionMarg{padding-top:140px;}
.Download .PositionMarg p{color:white;}
/* .Download .PositionMarg .DownLinks{padding:5px;} */
.Download .PositionMarg .DownLinks a svg{height:35px;width:35px;padding:10px;background:rgba(0,0,0,0.9);color:white;border:2px solid white;margin-left:5px;transform:translate(-2.5px,0);}
/* .Download .PositionMarg .DownLinks a{padding:35px;border:2px solid white;color:white;text-decoration:none;background:rgba(0,0,0,0.9);} */
.Download .PositionMarg .DownLinks a svg:hover{background:gray;}