.Footer{width:calc(100% - 20px);display:flex;padding:10px;z-index:10;}
.Footer .FootLinks{width:50%;text-align:left;margin-top:10px;}
.Footer .FootLinks a{padding-left:20px;text-decoration:none;color:rgba(0,0,0,0.9);}
.Footer .FootLinks span{color:rgba(0,0,0,0.9);cursor:pointer;}
.Footer .FootIcons{width:50%;text-align:right;}
.Footer .FootIcons a svg{width:35px;height:35px;padding-right:10px;}