.Menu{background:rgba(0,0,0,0.7);top:0;float:left;position:sticky;margin:0;width:100%;height:50px;display:flex;z-index:10;}
/* .Menu div{width:%} */
.Menu .Logo{text-align:left;width:75%;display:flex;}
.Menu .Logo img{height:40px;padding:5px;}
.Menu .Logo h1{color:white;display:flex;font-size:15px;padding-left:15px;margin-top:15px;}
.Menu .Items{display:flex}
.Menu .Items a{padding:15px;width:20%;display:inline;color:white;text-decoration:none;height:50px;}