@media screen and (max-width: 640px){
	.Technology{display:flex;flex-direction:column-reverse;}
	.Technology .Tech{width:100%;}
	.Technology .Tech img{width:100%;margin-left:0;}
	.Technology .Team{width:100%;margin-top:-5px;}
	.Technology .Team .CenterContent h4{color:white;}
	.Technology .Team .CenterContent p{width:80%;color:beige;margin-left:calc(10% - 20px);padding:20px;}
	.Technology .Team .CenterContent table tfoot tr td button{width:40px;height:40px;margin:10px;}
}